"use client";

import {
  Box,
  Container,
  Heading,
  Text,
  VStack,
  HStack,
  Image,
  Flex,
} from "@chakra-ui/react";

const initiatives = [
  {
    name: "Trinity Force",
    logo: "/images/logos/trinity-force-light-mode.png",
    description:
      "Our flagship robotics program that introduces students to engineering principles, programming, and teamwork through competitive robotics challenges. Students build, code, and compete while developing critical thinking skills.",
    imageOnLeft: true,
  },
  {
    name: "ByteForce",
    logo: "/images/logos/byte-force-light-mode.png",
    description:
      "A coding and software development initiative that teaches programming fundamentals, web development, and app creation. Students learn industry-standard languages and tools while working on real-world projects.",
    imageOnLeft: false,
  },
  {
    name: "Cultivation Camps",
    logo: "/images/logos/cultivation-camps.png",
    description:
      "Immersive summer and weekend camps that provide intensive STEM experiences. From science experiments to tech projects, our camps cultivate curiosity and inspire the next generation of innovators.",
    imageOnLeft: true,
  },
];

function InitiativeRow({ name, logo, description, imageOnLeft }) {
  const imageContent = (
    <Box
      flex={{ base: "none", md: 1 }}
      display="flex"
      justifyContent="center"
      alignItems="center"
      p={8}
    >
      <Image
        src={logo}
        alt={`${name} Logo`}
        maxW={{ base: "200px", md: "250px" }}
        fallbackSrc={`https://via.placeholder.com/250x150?text=${name.replace(
          " ",
          "+"
        )}`}
      />
    </Box>
  );

  const textContent = (
    <VStack
      flex={{ base: "none", md: 1 }}
      align={{ base: "center", md: imageOnLeft ? "flex-start" : "flex-end" }}
      textAlign={{ base: "center", md: imageOnLeft ? "left" : "right" }}
      spacing={4}
      p={8}
    >
      <Heading as="h3" size="lg" color="brand.600">
        {name}
      </Heading>
      <Text color="gray.600" fontSize="lg">
        {description}
      </Text>
    </VStack>
  );

  return (
    <Flex
      direction={{ base: "column", md: "row" }}
      align="center"
      w="100%"
      py={8}
      borderBottomWidth={1}
      borderBottomColor="gray.200"
      _last={{ borderBottom: "none" }}
    >
      {imageOnLeft ? (
        <>
          {imageContent}
          {textContent}
        </>
      ) : (
        <>
          {textContent}
          {imageContent}
        </>
      )}
    </Flex>
  );
}

export default function InitiativesSection() {
  return (
    <Box py={{ base: 12, md: 20 }}>
      <Container maxW="container.xl">
        <VStack spacing={8}>
          {/* Section Header */}
          <VStack spacing={4} textAlign="center" mb={8}>
            <Heading as="h2" size="xl" color="gray.800">
              Our Initiatives
            </Heading>
            <Text color="gray.600" fontSize="lg" maxW="2xl">
              Discover the programs that drive our mission to empower youth
              through STEM education.
            </Text>
          </VStack>

          {/* Initiative Rows */}
          <Box w="100%">
            {initiatives.map((initiative) => (
              <InitiativeRow
                key={initiative.name}
                name={initiative.name}
                logo={initiative.logo}
                description={initiative.description}
                imageOnLeft={initiative.imageOnLeft}
              />
            ))}
          </Box>
        </VStack>
      </Container>
    </Box>
  );
}
